import { mdiDownload, mdiFileDocument, mdiFileSearch } from '@mdi/js';
import Icon from '@mdi/react';
import { ReactElement } from 'react';
import { SetterOrUpdater, useSetRecoilState } from 'recoil';
import { reportAtom } from '../../global/state';
import './Dropper.scss';

interface ReportProps {
	flag: boolean,
	list: string[][]
}

/**
 * ÎìúÎ°úÌçº ReactElement Î∞òÌôò Î©îÏÑúÎìú
 *
 * @returns {ReactElement} Ïª¥Ìè¨ÎÑåÌä∏ ReactElement
 */
export default function Dropper(): ReactElement
{
	const setReportState = useSetRecoilState(reportAtom);

	return (
		<div id="dropper"
			onDrop={(e) =>
			{
				e.preventDefault();

				const data = e.dataTransfer.files[0];

				// dataÍ∞Ä Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
				if (data)
				{
					csv2arr(data, setReportState);
				}
			}}

			onDragOver={(e) =>
			{
				e.preventDefault();
			}}
		>
			<p>
				<Icon path={mdiFileDocument} size={3} />
			</p>

			<p>CSV ÏñëÏãùÏùÑ Drag &amp; Drop ÌïòÍ±∞ÎÇò, ÏïÑÎûòÏùò Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠Ìï¥ÏÑú ÌååÏùºÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî.</p>

			<input type="file" onChange={(e) =>
			{
				e.preventDefault();

				// e.targetÏù¥ Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
				if (e.target)
				{
					const file = e.target.files;

					// fileÏù¥ Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
					if (file)
					{
						csv2arr(file[0], setReportState);
					}
				}
			}} />

			<div>
				<button className="icon-button" data-active="download" onClick={() => open('/klid/sheet.csv')}>
					<Icon path={mdiDownload} size={0.75} /> ÏñëÏãù Îã§Ïö¥Î°úÎìú
				</button>

				<button className="icon-button" data-active="choose" onClick={() =>
				{
					// documentÍ∞Ä Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
					if (document)
					{
						const file: HTMLInputElement | null = document.querySelector('input[type="file"]');

						// fileÏù¥ Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
						if (file)
						{
							file.accept = '.csv';
							file.click();
						}
					}
				}}>
					<Icon path={mdiFileSearch} size={0.75} /> ÌååÏùº ÏÑ†ÌÉùÌïòÍ∏∞
				</button>
			</div>
		</div>
	);
}

/**
 * CSV to Î∞∞Ïó¥ Î≥ÄÌôò Î∞è Î∞òÌôò Î©îÏÑúÎìú
 *
 * @param {File} file: ÌååÏùº Í∞ùÏ≤¥
 * @param {ReportProps}
 */
function csv2arr(file: File, setter: SetterOrUpdater<ReportProps>)
{
	if (file)
	{
		const reader = new FileReader();
		const extension = file.name.split('.').pop() as string;
		const isAllow = [ 'csv' ].indexOf(extension) !== -1;

		// ÌóàÏö©Îêú ÌôïÏû•ÏûêÏùº Í≤ΩÏö∞
		if (isAllow)
		{
			reader.readAsText(file, 'EUC-KR');
			reader.onload = (e) =>
			{
				// e.targetÏù¥ Ïú†Ìö®Ìïú Í∞ùÏ≤¥Î•º Í∞ÄÏßà Í≤ΩÏö∞
				if (e.target)
				{
					const text = e.target.result as string;
					const arr = text.split('\r\n').map(e => e.split(',')).slice(1);

					setter({
						flag: true,
						list: arr
					});
				}

				// ÏïÑÎãê Í≤ΩÏö∞
				else
				{

				}
			};
		}

		// ÏïÑÎãê Í≤ΩÏö∞
		else
		{
			alert('ÏßÄÏõêÌïòÏßÄ ÏïäÎäî ÌôïÏû•Ïûê');
		}
	}
}